import { Component, OnInit } from '@angular/core';
import { Observable } from 'rxjs';
import { RemoteControlService, RemoteControl } from 'remote-control';
import { MatBottomSheet, MatBottomSheetRef } from '@angular/material';
import { LanguagesComponent } from './languages/languages.component';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: [ './header.component.scss' ]
})
export class HeaderComponent implements OnInit {
  public controllers$ = this.remote.remoteControls$;
  public control$ = this.remote.remoteControl$;
  public competition$: Observable<any> | undefined = undefined;
  public stage$: Observable<any> | undefined = undefined;
  public discipline$: Observable<any> | undefined = undefined;

  constructor(private _bottomSheet: MatBottomSheet, private remote: RemoteControlService) {}
  ngOnInit() {}

  setControl(control: RemoteControl) {
    this.remote.remoteControl = control;
  }

  openLanguageSelect() {
    this._bottomSheet.open(LanguagesComponent);
  }
}
