import { Component, OnInit } from '@angular/core';
import { InteractionService } from 'interaction';
import { HttpClient } from '@angular/common/http';
import { ActivatedRoute, Router } from '@angular/router';
import { map, mergeMap } from 'rxjs/operators';

@Component({
  selector: 'app-tracker',
  templateUrl: './tracker.component.html',
  styleUrls: [ './tracker.component.scss' ]
})
export class TrackerComponent implements OnInit {
  public tracker$ = undefined;
  public tracker = this.route.snapshot.queryParams.tracker || '0000';

  constructor(
    private inter: InteractionService,
    private http: HttpClient,
    private route: ActivatedRoute,
    private router: Router
  ) {
    this.tracker$ = this.route.queryParams.pipe(
      mergeMap((_) => this.http.get(`${this.inter.originByModule('log-adapter')}/api/v1/trackers/${_.tracker}`))
    );
  }

  ngOnInit() {}
}
