import { Component, OnInit } from '@angular/core';
import { SharedData } from '../system-sort.pipe';
import { ActivatedRoute, Router } from '@angular/router';
import { map, tap, startWith } from 'rxjs/operators';
import { Location } from '@angular/common';
import { InteractionService } from 'interaction';

@Component({
  selector: 'app-intern',
  templateUrl: './intern.component.html',
  styleUrls: [ './intern.component.scss' ]
})
export class InternComponent implements OnInit {
  private path = [];
  public currentPath = this.router.events.pipe(
    startWith(0),
    map((val) => this.location.path()),
    map((_) => _.split('?')[0].split('/').slice(1)),
    tap((_) => (this.path = _))
  );
  public back() {
    return [ '/', ...this.path ].slice(0, -1);
  }
  public direct(level: number) {
    return [ '/', ...this.path ].slice(0, level + 2);
  }

  constructor(private inter: InteractionService, private location: Location, private router: Router) {}

  ngOnInit() {}
}
