import { Component, OnInit } from '@angular/core';
import { InteractionService } from 'interaction';
import { Router, ActivatedRoute } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { config as configCpu } from './chartsConfigCPU';
import { config as configMemory } from './chartsConfigMemory';
import { EChartOption } from 'echarts';
import { mergeMap, map, switchMap, tap } from 'rxjs/operators';
import { combineLatest } from 'rxjs';
import { DetailComponent } from './detail/detail.component';
import { MatBottomSheet } from '@angular/material';

@Component({
  selector: 'app-system',
  templateUrl: './system.component.html',
  styleUrls: [ './system.component.scss' ]
})
export class SystemComponent implements OnInit {
  public historyCpu = { xAxis: { data: Array(60).join('.').split('.') }, series: [] };
  public historyMemory = { xAxis: { data: Array(60).join('.').split('.') }, series: [] };
  public modules = undefined;

  public details$;
  public system$ = this.system.systemModules$.pipe(
    tap((_) => (this.modules = _)),
    mergeMap((_) =>
      combineLatest(
        ...Object.keys(_).map((moduleKey) =>
          this.system
            .dataStreamByModule<{ [key: string]: number }>(moduleKey, 'metrics', '/metrics', false)
            .pipe(map((__) => ({ name: moduleKey, data: __ })))
        )
      )
    ),
    tap((datas) => datas.forEach((_) => (this.metrics[_.name] = _.data)))
  );
  public metrics = {};
  public int = setInterval(() => {
    this.historyCpu = this.refreshChart(this.historyCpu, 'cpu');
    this.historyMemory = this.refreshChart(this.historyMemory, 'memory');
  }, 1000);

  public chartOptionCpu: EChartOption | any = configCpu;
  public chartOptionMemory: EChartOption | any = configMemory;

  constructor(
    private system: InteractionService,
    private http: HttpClient,
    private route: ActivatedRoute,
    private _bottomSheet: MatBottomSheet
  ) {
    /*    this.details$ = route.queryParams.pipe(
      map((_) => _.module),
      tap((_) => {
        if (!!_)
          this._bottomSheet.open(DetailComponent, {
            panelClass: 'bottomsheet--details',
            data: { system$: this.system$, module: _ }
          });
      })
    );*/
  }

  private refreshChart(chart: any, type: 'cpu' | 'memory'): any {
    Object.keys(this.metrics).forEach((moduleKey) => {
      let moduleChart = chart.series.find((_) => _.name === moduleKey);
      if (!moduleChart) {
        moduleChart = {
          name: moduleKey,
          type: 'line',
          smooth: true,
          showAllSymbol: true,
          symbol: 'emptyCircle',
          symbolSize: 0,
          data: Array(60).map((_) => 0)
        };
        chart.series.push(moduleChart);
      }

      if (moduleChart.data.length > 60) moduleChart.data.shift();
      moduleChart.data.push(
        Math.round((type === 'memory'
          ? this.formatMemory(this.metrics[moduleKey])
          : this.formatCpu(this.metrics[moduleKey])) as number)
      );
      //      if (chart.series[1].data.length > 60) this.history.series[1].data.shift();
      //      this.history.series[1].data.push(_.memUsedAll / 1024 / 1024);
    });
    return { ...chart };
  }

  public formatCpu(metrics: { [key: string]: number }, unit?: boolean) {
    if (!!unit) return Math.round(metrics.cpu) + '%';
    return Math.round(metrics.cpu);
  }
  public formatMemory(metrics: { [key: string]: number }, unit?: boolean) {
    if (!!unit) return Math.round(metrics.memUsedAll / 1024 / 1024) + 'MB';
    return Math.round(metrics.memUsedAll / 1024 / 1024);
  }
  public openDetails(moduleName: string) {
    this._bottomSheet.open(DetailComponent, {
      panelClass: 'bottomsheet--details',
      data: { system$: this.system$, module: moduleName }
    });
  }

  ngOnInit() {}
}
