import { Component, OnInit, Inject } from '@angular/core';
import { MAT_BOTTOM_SHEET_DATA } from '@angular/material';
import { InteractionService } from 'interaction';
import { map } from 'rxjs/operators';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-detail',
  templateUrl: './detail.component.html',
  styleUrls: [ './detail.component.scss' ]
})
export class DetailComponent implements OnInit {
  public logs$ = this.system.arrayStreamByModule<{ tracker: string; level: string; message: string }>(
    'log-adapter',
    `logs/${this.data.module}`,
    `/api/v1/logs/${this.data.module}`,
    'before'
  );
  public metrics$ = this.data.system$.pipe(map((datas: any[]) => datas.find((_) => _.name === this.data.module)!.data));
  public apis$ = this.http.get(`${this.system.originByModule(this.data.module)}/api/latest`);

  constructor(
    private system: InteractionService,
    private http: HttpClient,
    @Inject(MAT_BOTTOM_SHEET_DATA) public data: { system$: any; module: string }
  ) {}

  ngOnInit() {}
}
