import { NgModule, Injectable } from '@angular/core';
import { RouterModule, Routes, CanActivate } from '@angular/router';
import { DashboardComponent } from './dashboard/dashboard.component';
import { HeaderComponent } from './header/header.component';
import { InternMenuComponent } from './intern-menu/intern-menu.component';
import { InternComponent } from './intern/intern.component';
import { LoginComponent } from './login/login.component';
import { PortalComponent } from './portal/portal.component';
import { mergeMap } from 'rxjs/operators';
import { AuthorizationService, AuthGuard } from 'authorization';
import { InteractionService } from 'interaction';
import { environment } from 'src/environments/environment';
import { TranslationService } from 'translation';
import { RemoteControlService } from 'remote-control';
import { IntroComponent } from './intro/intro.component';
import { SystemComponent } from './system/system.component';
import { TrackerComponent } from './system/tracker/tracker.component';

@Injectable({
  providedIn: 'root'
})
export class InitGuard implements CanActivate {
  constructor(
    private inter: InteractionService,
    private trans: TranslationService,
    private auth: AuthorizationService,
    private remote: RemoteControlService
  ) {}

  canActivate() {
    return this.inter
      .init(environment.systemEndpoint)
      .pipe(
        mergeMap((_) => this.auth.init()),
        mergeMap((_) =>
          this.trans.init((window.navigator.languages || [
            window.navigator.language || (window.navigator as any).userLanguage
          ]) as string[])
        ),
        mergeMap((_) => this.remote.init())
      );
  }
}

@Injectable({
  providedIn: 'root'
})
export class SystemGuard implements CanActivate {
  constructor(private inter: InteractionService, private auth: AuthorizationService) {}

  canActivate() {
    return this.inter.init(environment.systemEndpoint).pipe(mergeMap((_) => this.auth.init()));
  }
}

const routes: Routes = [
  {
    path: '',
    canActivate: [ SystemGuard ],
    outlet: 'header',
    component: HeaderComponent
  },
  {
    path: '',
    canActivate: [ SystemGuard ],
    children: [
      {
        path: '',
        canActivate: [ InitGuard ],
        pathMatch: 'full',
        component: DashboardComponent
      },
      {
        path: 'intro',
        component: IntroComponent
      },
      {
        path: 'login',
        component: LoginComponent
      },
      {
        path: 'intern',
        component: InternComponent,
        children: [
          {
            path: '',
            component: InternMenuComponent,
            pathMatch: 'full'
          },
          {
            path: 'system',
            component: SystemComponent
          },
          {
            path: 'tracker',
            component: TrackerComponent
          },
          {
            path: 'organization',
            component: PortalComponent
          }
        ]
      }
    ]
  }
];

@NgModule({
  imports: [ RouterModule.forRoot(routes) ],
  providers: [],
  exports: [ RouterModule ]
})
export class AppRoutingModule {}
