import { Component, OnInit, Renderer2, Inject } from '@angular/core';
import { InteractionService } from 'interaction';
import { map, take } from 'rxjs/operators';
import { environment } from 'src/environments/environment';
import { Observable } from 'rxjs';
import { Meta } from '@angular/platform-browser';
import { DOCUMENT } from '@angular/common';
import { RemoteControlService, RemoteControl } from 'remote-control';
import { MatSnackBar } from '@angular/material';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: [ './dashboard.component.scss' ]
})
export class DashboardComponent implements OnInit {
  public apps$: Observable<{ name: string; id: string }[]> = this.inter.systemModules$.pipe(
    map((_) =>
      [].concat.apply(
        [],
        Object.values(_).map((__: any) =>
          Object.keys(__.apps).filter((___) => ___.startsWith('/dashboard/')).map((___: any) => {
            __.apps[___].name = ___;
            return __.apps[___];
          })
        )
      )
    ),
    take(1)
  );

  public controllers$ = this.remote.remoteControls$;
  public control$ = this.remote.remoteControl$;

  constructor(
    private inter: InteractionService,
    private meta: Meta,
    private renderer2: Renderer2,
    private remote: RemoteControlService,
    @Inject(DOCUMENT) private document: Document,
    private _snackBar: MatSnackBar
  ) {
    this.meta.addTag({ name: 'importmap-type', content: 'systemjs-importmap' });
    this.openSnackBar(
      'Currently under development - updates are applied frequently. Project started 26.12.2019',
      'Close'
    );
  }

  openSnackBar(message: string, action: string) {
    this._snackBar.open(message, action, {
      duration: 20000
    });
  }

  setControl(control: RemoteControl) {
    this.remote.remoteControl = control;
  }

  ngOnInit() {
    this.apps$.subscribe((_) => {
      //      let imports = JSON.parse(this.document.getElementById('import-map').textContent.replace(/\r?\n|\r/g, ''));
      const loadImports = {};

      _.forEach((item) => {
        loadImports[item.id || 'coma'] = `${!environment.production ? 'http://localhost:80' : ''}${item.name.length ===
        1
          ? item.name
          : item.name + '/'}main-es2015.js`;
      });

      localStorage.setItem(
        'slots',
        JSON.stringify({
          'barbell-display': 'slot1',
          'sequence-display': 'slot2',
          'referee-dashboard': 'slot3'
        })
      );

      setTimeout(() => {
        let embScript = this.document.getElementById('spa-loader') as HTMLScriptElement;
        if (!embScript) {
          embScript = this.renderer2.createElement('script');
        } else {
          embScript.remove();
        }

        embScript.async = false;
        embScript.text = `
            System.import('single-spa').then(function (singleSpa) {
              ${Object.keys(loadImports)
                .map(
                  (__) => `singleSpa.registerApplication(
                    '${__}',
                    function () {
                      console.log('test');
                      return System.import('${__}');
                    },
                    function (location) {
                      console.log(location.pathname, location.pathname.startsWith('/'));
                      return location.pathname.startsWith('/');
                    }
                  );`
                )
                .join('')}
              singleSpa.start();
            });`;
        this.renderer2.appendChild(this.document.body, embScript);
      }, 1000);
    });
  }
}
